const state = {
    teamEn:''
  }
  
  const getters = {
    getTeam: state => state.teamEn
  }
  
  const mutations = {
    // 
    teamEnStatus(state,teamEn){
      if(teamEn){
        state.teamEn = teamEn
      }else{
        state.teamEn = null
      }
    }
  }
  
  const actions = {
    // mutations
    setTeam({commit},teamEn){
      commit("teamEnStatus",teamEn)
    }
  }
  
  export default{
    state,
    getters,
    mutations,
    actions
  }


