<template>
  <div class="clearfix">
    <Menu
      :active-name="menu.activeName"
      ref="activeName"
      :open-names="[menu.activeopenNames]"
      theme="dark"
      class="sidebar"
    >
      <img id="mirror" src="../../../../static/images/icon.png" title="" />
      <Submenu name="team">
        <template slot="title">
          <Icon type="ios-hammer" />
          
        </template>
        <MenuItem name="iteam" to="/team/iteam"></MenuItem>
        <MenuItem name="creatteam" to="/team/creatteam"></MenuItem>
      </Submenu>
      <vue-scroll :ops="ops" v-if="this.$store.getters.getTeam">
        <Submenu name="overview">
          <template slot="title">
            <Icon type="ios-eye-outline" />
            
          </template>
          <MenuItem name="kanban" to="/overview/kanban"></MenuItem>
        </Submenu>
        <Submenu name="behavior">
          <template slot="title">
            <Icon type="ios-filing" />
            
          </template>
          <MenuItem name="pageOverView" to="/behavior/pageOverView"
            ></MenuItem
          >
          <MenuItem name="eventOverView"to="/behavior/eventOverView"></MenuItem>
        </Submenu>
        <Submenu name="user">
          <template slot="title">
            <Icon type="md-contact" />
            
          </template>
          <MenuItem name="userFeatures" to="/user/userFeatures"></MenuItem>
        </Submenu>
      </vue-scroll>
    </Menu>
    <Header class="layout-header">
      <Menu mode="horizontal" theme="light" active-name="1">
        <div class="layout-logo">-</div>
        <div class="layout-logo layout-team">
          {{ this.$store.getters.getTeam }}
          <router-link to="/team/iteam">
            <span class="ivu-team"></span>
          </router-link >
        </div>
        <div class="layout-nav">
          <Dropdown
            trigger="click"
            style="margin-left: 20px;margin-right: 20px;"
          >
            <a href="javascript:void(0)">
              <Icon type="md-contact" class="icon-color" />
              <span id="userName">{{ mobile }}</span>
            </a>
            <DropdownMenu slot="list" class="DropdownMenu">
              <DropdownItem><span @click="logout"></span></DropdownItem>
            </DropdownMenu>
          </Dropdown>
        </div>
      </Menu>
    </Header>
    <div class="meta-title content-width-right">
      {{this.$route.meta.title}}
    </div>
  </div>
</template>
<script>
import Fetcher from "./../../../assets/fetcher";
export default {
  data() {
    return {
      ops: {
        vuescroll: {},
        scrollPanel: {},
        rail: {
          keepShow: true
        },
        bar: {
          hoverStyle: true,
          onlyShowBarOnScroll: false, //
          background: "#80869280"
        }
      },
      mobile: "",
      menu: {
        activeName: "",
        activeopenNames: []
      }
    };
  },
  methods: {
    async logout() {
      let data = await Fetcher.logout(this);
      if (data.code === 200) {
        this.$Message.success("");
        this.$store.dispatch("setUser", null);
        window.postMirrorData.entities.user='';
        this.$store.dispatch("setTeam", "");
        this.$router.push("/registerAndLogin");
      } else {
        this.$Message.error(data.message);
      }
    }
  },
  created() {
    this.mobile = this.$store.getters.getUser;
  },
  mounted() {
    this.menu = {
      activeName: this.$route.path.slice(1).split("/")[1],
      activeopenNames: this.$route.path.slice(1).split("/")[0]
    };
    this.$nextTick(() => {
      this.$refs.activeName.updateOpened();
      this.$refs.activeName.updateActiveName();
    });
  },
  
watch: {
  $route: {
    handler: function(val, oldVal){
      this.menu = {
      activeName: val.path.slice(1).split("/")[1],
      activeopenNames: val.path.slice(1).split("/")[0]
    };
    setTimeout(()=>{
      this.$refs.activeName.updateOpened();
      this.$refs.activeName.updateActiveName();
    },0)
    },
    // 
    deep: true
  }
}
};
</script>
<style lang="less">
#mirror {
  width: 20%;
  align-self: center; 

}
.meta-title {
  font-size: 24px;
  color: #000;
  text-align: left;
  padding: 10px;
  margin-top: 60px;
}
.ivu-team {
  height: 30px;
  display: inline-block;
  background: #3ad0d7;
  line-height: 30px;
  border-radius: 5px;
  color: #fff;
}
.layout-team {
  position: absolute;
  right: 220px;
}
.DropdownMenu {
  width: 130px;
}
.sidebar {
  z-index: 1020;
  width: 240px;
  height: 100%;
  background: #35404f;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  padding: 0 0 50px;
  transition: width 0.2s, left 0.2s;
  box-shadow: 2px 0 10px 0 rgba(166, 166, 166, 0.5);
}
.ivu-menu-dark.ivu-menu-vertical .ivu-menu-opened {
  margin-right: 10px;
}
.layout-header {
  width: calc(100% - 240px);
  position: fixed;
  top: 0;
  right: 0;
  padding: 0;
  height: auto;
  z-index: 999;
  background: #fff;
  box-shadow: 0 1px 1px 0 rgba(166, 166, 166, 0.2);
  transition: margin-left 0.2s ease;
  .layout-logo {
    display: inline-block;
    color: #3ad0d7;
  }
  .layout-nav {
    display: inline-block;
    margin-right: 40px;
    float: right;
    &:hover {
      background: #eaf2f3;
    }
  }
}
.icon-color {
  color: #3ad0d7;
  font-size: 40px;
}
#userName {
  color: #3ad0d7;
}
</style>



