<template>
  <div class="registerBG">
    <Modal
      v-model="modal"
      width="600"
      class-name="vertical-center-modal"
      :closable="false"
      :mask-closable="false"
    >
      <p slot="header" style="color:#f60;text-align:center"></p>
      <Row :gutter="32">
        <Col span="24" class="demo-tabs-style1">
          <Tabs>
            <TabPane label="">
              <Form
                ref="Login"
                :model="Login"
                class="signin"
                style="margin-top:30px"
              >
                <FormItem prop="mobile">
                  <Input
                    type="text"
                    v-model="Login.mobile"
                    placeholder="11"
                    :autofocus="true"
                     @on-keyup.enter="loginSubmit"
                  >
                    <Icon type="ios-call" slot="prepend"></Icon>
                  </Input>
                </FormItem>
                <FormItem prop="password">
                  <Input
                    type="password"
                    v-model="Login.password"
                    placeholder="6+"
                    :autofocus="false"
                     @on-keyup.enter="loginSubmit"
                  >
                    <Icon type="md-lock" slot="prepend"></Icon>
                  </Input>
                </FormItem>
                <FormItem style="text-align:center">
                  <Button
                    type="primary"
                    @click="loginSubmit"
                    style="width:30%;font-size:16px"
                    ></Button
                  >
                </FormItem>
              </Form>
            </TabPane>
            <TabPane label="">
              <Form
                ref="Register"
                :model="Register"
                class="signup"
                style="margin-top:30px"
              >
                <FormItem prop="mobile">
                  <Input
                    type="text"
                    v-model="Register.mobile"
                    placeholder="11"
                    style="text-align:center"
                    @on-keyup.enter="registerSubmit"
                  >
                    <Icon type="ios-call" slot="prepend"></Icon>
                  </Input>
                </FormItem>
                <FormItem prop="password">
                  <Input
                    type="password"
                    v-model="Register.password"
                    placeholder="6+"
                     @on-keyup.enter="registerSubmit"
                  >
                    <Icon type="md-lock" slot="prepend"></Icon>
                  </Input>
                </FormItem>
                <FormItem style="text-align:center">
                  <Button
                    type="primary"
                    @click="registerSubmit"
                    style="width:30%;font-size:16px"
                    ></Button
                  >
                </FormItem>
              </Form>
            </TabPane>
          </Tabs>
        </Col>
      </Row>
      <div slot="footer" style="text-align:center">
        <p></p>
      </div>
    </Modal>
  </div>
</template>

<script>
import  Fetcher from "../../assets/fetcher";
export default {
  name: "RegiterLogin",
  data() {
    return {
      modal: true,
      Login: {
        mobile: "",
        password: ""
      },
      Register: {
        mobile: "",
        password: ""
      }
    };
  },
 
  methods: {
    async loginSubmit() {
      const mobileReg = /^[1][3,4,5,7,8][0-9]{9}$/;
      const passwordReg = /(?![0-9]+$)(?![a-zA-Z]+$)[\da-zA-Z]{6,}/;
      if (
        mobileReg.test(this.Login.mobile) &&
        passwordReg.test(this.Login.password)
      ) {
        
        const params = {
          mobile: this.Login.mobile,
          password: this.Login.password
        };
        let data = await Fetcher.login(this,params);
        if (data.code === 200) {
              this.$Message.success("");
               this.$store.dispatch("setUser",this.Login.mobile);
               
               window.postMirrorData.entities.user=this.Login.mobile;
               Fetcher.postEventViewData({
                action:'',
                category:''
              })
               this.$store.getters.getTeam?this.$router.push('/overview/kanban'):this.$router.push('/team/iteam');
            } else {
              this.$Message.error(data.message);
            }
       } else {
        this.$Message.error("");
       }
    },
    async registerSubmit() {
      const mobileReg = /^[1][3,4,5,7,8][0-9]{9}$/;
      const passwordReg = /(?![0-9]+$)(?![a-zA-Z]+$)[\da-zA-Z]{6,}/;
      if (
        mobileReg.test(this.Register.mobile) &&
        passwordReg.test(this.Register.password)
      ) {
        Fetcher.postEventViewData({
                action:'',
                category:''
              })
        const params = {
          mobile: this.Register.mobile,
          password: this.Register.password
        };
        let data = await Fetcher.register(this,params);
        if (data.code === 200) {
              this.$Message.success("");
            } else {
              this.$Message.error(data.message);
            }
      } else {
        this.$Message.error("");
      }
    }
  }
};
</script>
<style lang="less">
// /scoped/
.ivu-tabs-nav {
  left: 50%;
  transform: translateX(-50%);
  font-size: 20px;
}
// //
.qqwximg {
  height: 38px;
}

.registerBG {
  background: url("./../../../static/images/bg.jpg") no-repeat;
  background-size: cover;
  width: 100%;
  height: 100%;
}

.vertical-center-modal {
  display: flex;
  align-items: center;
  justify-content: center;

  .ivu-modal {
    top: 0;
  }
}
</style>


